export default function PemesananPage() {
  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-md">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <h1 className="text-2xl font-bold text-gray-900">Biro Perjalanan Wisata</h1>
            <nav className="space-x-4">
              <a href="/" className="text-gray-700 hover:text-blue-600">Beranda</a>
              <a href="/kontak" className="text-gray-700 hover:text-blue-600">Kontak</a>
              <a href="/pemesanan" className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700">Pesan Sekarang</a>
            </nav>
          </div>
        </div>
      </header>

      {/* Content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="max-w-2xl mx-auto">
          <h2 className="text-3xl font-bold text-center mb-8">Form Pemesanan</h2>

          <div className="bg-white rounded-lg shadow-md p-8">
            <form className="space-y-6">
              <div>
                <label htmlFor="nama" className="block text-sm font-medium text-gray-700 mb-2">
                  Nama Lengkap
                </label>
                <input
                  type="text"
                  id="nama"
                  name="nama"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
                  Email
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              <div>
                <label htmlFor="telepon" className="block text-sm font-medium text-gray-700 mb-2">
                  Nomor Telepon
                </label>
                <input
                  type="tel"
                  id="telepon"
                  name="telepon"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              <div>
                <label htmlFor="destinasi" className="block text-sm font-medium text-gray-700 mb-2">
                  Pilih Destinasi
                </label>
                <select
                  id="destinasi"
                  name="destinasi"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                >
                  <option value="">Pilih destinasi...</option>
                  <option value="ijen">Gunung Ijen (Blue Fire)</option>
                  <option value="bromo">Gunung Bromo</option>
                  <option value="tumpak-sewu">Air Terjun Tumpak Sewu</option>
                </select>
              </div>

              <div>
                <label htmlFor="tanggal" className="block text-sm font-medium text-gray-700 mb-2">
                  Tanggal Keberangkatan
                </label>
                <input
                  type="date"
                  id="tanggal"
                  name="tanggal"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              <div>
                <label htmlFor="jumlah" className="block text-sm font-medium text-gray-700 mb-2">
                  Jumlah Peserta
                </label>
                <input
                  type="number"
                  id="jumlah"
                  name="jumlah"
                  min="1"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>

              <div>
                <label htmlFor="paket" className="block text-sm font-medium text-gray-700 mb-2">
                  Pilih Paket
                </label>
                <select
                  id="paket"
                  name="paket"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                >
                  <option value="">Pilih paket...</option>
                  <option value="premium">Paket Premium</option>
                  <option value="standard">Paket Standard</option>
                  <option value="budget">Paket Budget</option>
                </select>
              </div>

              <div>
                <label htmlFor="catatan" className="block text-sm font-medium text-gray-700 mb-2">
                  Catatan Tambahan
                </label>
                <textarea
                  id="catatan"
                  name="catatan"
                  rows={3}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder="Kebutuhan khusus, alergi, dll..."
                ></textarea>
              </div>

              <div className="bg-gray-50 p-4 rounded-lg">
                <h3 className="text-lg font-semibold mb-2">Ringkasan Pemesanan</h3>
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span>Destinasi:</span>
                    <span id="summary-destinasi">-</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Jumlah Peserta:</span>
                    <span id="summary-jumlah">-</span>
                  </div>
                  <div className="flex justify-between">
                    <span>Paket:</span>
                    <span id="summary-paket">-</span>
                  </div>
                  <div className="flex justify-between font-semibold text-lg border-t pt-2">
                    <span>Total:</span>
                    <span id="summary-total">-</span>
                  </div>
                </div>
              </div>

              <div className="text-center">
                <button
                  type="submit"
                  className="bg-blue-600 text-white px-8 py-3 rounded-lg font-semibold hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  Konfirmasi Pemesanan
                </button>
              </div>
            </form>
          </div>

          <div className="mt-8 text-center text-sm text-gray-600">
            <p>Dengan mengirim form ini, Anda menyetujui syarat dan ketentuan kami.</p>
            <p>Pembayaran dapat dilakukan via transfer bank atau e-wallet.</p>
          </div>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-gray-800 text-white py-8">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <p>&copy; 2024 Biro Perjalanan Wisata. Semua hak dilindungi.</p>
        </div>
      </footer>
    </div>
  );
}